import axios from 'axios'
import QS from 'qs' // qspost
import { getItem } from './localstorage'
import Config from '../../config'
// 
if (process.env.NODE_ENV === 'development') {
  console.log('development')
  axios.defaults.baseURL = 'http://127.0.0.1:8888/api'
} else if (process.env.NODE_ENV === 'production') {
  axios.defaults.baseURL = 'http://real.breakinferno.cn/api'
} else {
  axios.defaults.baseURL = '127.0.0.1:8888/api'
}

axios.defaults.timeout = 10000

axios.interceptors.request.use(config => {
  let baseInfo = getItem(Config.baseDataName)
  if (baseInfo) {
    config.headers['access-token'] = JSON.parse(baseInfo).token
  }
  return config
}, err => {
  return Promise.reject(err)
})

axios.interceptors.response.use(
  response => {
    if (response.status === 200) {
      return Promise.resolve(response)
    } else {
      return Promise.reject(response)
    }
  },
  error => {
    if (error.response) {
      // switch (error.response.status) {
      //   case 401:
      //     break
      //   case 403:
      //     break
      //   case 404:
      //     break
      //   default:
      // }
    }
    return Promise.reject(error.response) // 
  })

/**
 * getget
 * @param {String} url [url]
 * @param {Object} params []
 */
export function get (url, params) {
  return new Promise((resolve, reject) => {
    axios.get(url, {
      params: params
    }).then(res => {
      resolve(res.data)
    }).catch(err => {
      reject(err.data)
    })
  })
}

/**
 * postpost
 * @param {String} url [url]
 * @param {Object} data []
 */
export function post (url, data) {
  return new Promise((resolve, reject) => {
    axios.post(url, QS.stringify(data))
      .then(res => resolve(res.data))
      .catch(err => reject(err.data))
  })
}
