// level1.level2
function getter (obj, path) {
  // obj[2][]
  const arrayRegx = /(\[)(\d+)(\])/g
  path = Array.isArray(path) ? path.join('.') : path
  path = path.replace(arrayRegx, '.$2').split('.') // key
  return path.reduce((target, p) => {
    return (target || {})[p]
  }, obj)
}
export default getter
